import http from "../utils/http";
export function getInvestLog(params) {
  return http.post("/api/invest/list", params);
}
export function getSwapLog(params) {
  return http.post("/api/swap/list", params);
}
export function getWithdrawLog(params) {
  return http.post("/api/withdraw/list", params);
}
export function getIntroLog(params) {
  return http.post("/api/introlog/list", params);
}

